:root {
  --White: white;
  --Action_Medium: #c3e0f4;
  --Group_Blue: #002b45;
  --Primary_Orange: #ed8b00;
  --No_Red: #be1e2d;
  --Link_Blue: #1b5786;
  --Text_Grey: #4d4e4c;
  --Text_Medium: #b8b8b7;
  --Action_Blue: #68b2e3; }

html, body {
  background-image: url(img/Background_Image.png);
  height: 100%;
  margin: 0px;
  padding: 0px;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  font-style: normal;
  font-stretch: normal;
  line-height: normal;
  font-family: DFDS; }

[class*="White-Surface-"] {
  background-color: white;
  border-radius: 2px;
  height: 100%; }

@media only screen and (min-width: 600px) {
  .White-Surface-T {
    width: 375px;
    height: 440px;
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%); } }

@media only screen and (min-width: 768px) {
  .White-Surface-D {
    width: 375px;
    height: 440px;
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%); } }

.Action-Blue {
  width: 44px;
  height: 44px;
  object-fit: contain;
  float: right;
  cursor: pointer; }

.Action-Blue:hover {
  opacity: 0.5; }

.header {
  height: 72px;
  padding-left: 20px;
  padding-top: 14px;
  padding-right: 20px; }

.logo {
  width: 92px;
  height: 32px;
  object-fit: contain; }
